@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&family=Roboto+Condensed:wght@700&display=swap');
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'roboto condensed';
	font-weight: bold;
}
body {
	background: #111;
	color: #fff;
	width: 100%;
	overflow-x: hidden;
}
ul,
ol,
li {
	list-style: none;
}
a {
	text-decoration: none;
}
fieldset {
	border: none;
}
table {
	border-collapse: collapse;
}
.h {
	position: absolute;
	top: -999999px;
	opacity: 0;
}
